/*
 *   Name: BizAgi Smartphone Workportal Facade
 *   Author: luisce
 *   Comments:
 *   -   This script will define a workportal facade to access to all components
 */

bizagi.workportal.tablet.facade.extend("bizagi.workportal.tablet.facade", {
    BIZAGI_NATIVE_COMPONENT_HOMEPORTAL: "homeportal"
}, {
    executeWebparts: function() {
        var self = this;
        return self._super.apply(this, arguments).then(function() {

            if (bizagi.util.isNativePluginSupported()) {
                if (bizagi.getNativeComponent() === self.Class.BIZAGI_NATIVE_COMPONENT_HOMEPORTAL) {
                    bizagiapp.homePortalReady();
                } else {
                    bizagiapp.workPortalReady();
                }
            }
        });
    },

    sortMenuItemsByStartPage: function(startpage, currentUser) {
        var self = this;

        // TODO: To review in iOS
        if (!bizagi.util.isAndroidDevice()) {
            self._super(startpage, currentUser);
        } else {
            if (typeof currentUser.associatedStakeholders !== "undefined" &&
                currentUser.associatedStakeholders.length > 0) {
                // Merge layouts - Load dashboard
                self.homePortalFramework.homePortalLayout = $.extend(self.homePortalFramework.homePortalLayoutDashboard,
                    self.homePortalFramework.homePortalLayoutComplement);
            } else {
                // Merge layouts - Load task feed
                self.homePortalFramework.homePortalLayout = $.extend(self.homePortalFramework.homePortalLayoutDashboard,
                    self.homePortalFramework.homePortalLayoutComplement);
            }
        }
    },

    loadModule: function(bizagiModule) {
        var self = this;
        var device = bizagi.util.detectRealDevice();

        if (typeof bizagiModule !== "string") return;

        //if (typeof self.modules[bizagiModule] !== 'undefined') {
        //    return self.modules[bizagiModule];
        //}

        self.modules[bizagiModule] = new $.Deferred();

        bizagi.loader.start(bizagiModule, device).then(function() {
            self.modules[bizagiModule].resolve();
        });

        return self.modules[bizagiModule].promise();
    }

});